module.exports = "/**\n * Applies styles for users in high contrast mode. Note that this only applies\n * to Microsoft browsers. Chrome can be included by checking for the `html[hc]`\n * attribute, however Chrome handles high contrast differently.\n * @param target Which kind of high contrast setting to target. Defaults to `active`, can be\n *    `white-on-black` or `black-on-white`.\n */\n/* Theme for the ripple elements.*/\n/* stylelint-disable material/no-prefixes */\n/* stylelint-enable */\n.example-container {\n  display: flex;\n  flex-direction: column;\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0; }\n.example-is-mobile .example-toolbar {\n  position: fixed;\n  /* Make sure the toolbar will stay on top of the content as it scrolls past. */\n  z-index: 2; }\nh1.example-app-name {\n  margin-left: 8px; }\n.example-sidenav-container {\n  /* When the sidenav is not fixed, stretch the sidenav container to fill the available space. This\r\n     causes `<mat-sidenav-content>` to act as our scrolling element for desktop layouts. */\n  flex: 1; }\n.example-is-mobile .example-sidenav-container {\n  /* When the sidenav is fixed, don't constrain the height of the sidenav container. This allows the\r\n     `<body>` to be our scrolling element for mobile layouts. */\n  flex: 1 0 auto; }\nmat-sidenav {\n  min-width: 150px;\n  background-color: lightgray; }\n.example-toolbar {\n  /*background: linear-gradient(to right, mat-color($primary-light, 50) 50%, mat-color($primary-light, 500));*/\n  background-color: #e3f2fd;\n  display: flex;\n  justify-content: space-between; }\n.dark-theme .example-toolbar {\n  /*background: linear-gradient(to right, mat-color($primary-light, 500) 50%, mat-color($primary-light, 50));*/\n  background-color: #2196f3; }\nmat-toolbar div {\n  display: flex;\n  align-items: center; }\n.content-container {\n  margin: 20px; }\nmat-nav-list {\n  /*Fix small gap at top of nav-list*/\n  margin-top: -8px;\n  display: flex;\n  flex-direction: column; }\n.sidenavNavList a {\n  border-style: solid;\n  border-width: 0px 0px 2px 0px; }\n.testnav {\n  flex: 1;\n  height: 45px;\n  display: flex;\n  align-items: center;\n  padding-left: 16px;\n  font-size: 16px;\n  font-family: sans-serif; }\n.testnav:hover {\n    background-color: #64b5f6; }\n#firstnav {\n  border-width: 2px 0px 2px 0px; }\n.toolbarNavList {\n  display: flex;\n  flex-direction: row; }\n.toolbarNavList a {\n    margin: 0px 10px 0px 10px;\n    padding: 4px; }\n.toolbarNavList a:hover {\n      color: #1976d2; }\n"