import { Component, OnInit } from '@angular/core';
import { MediaMatcher } from '@angular/cdk/layout';
import { ChangeDetectorRef, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-toolbar-and-sidenav-responsive',
  templateUrl: './toolbar-and-sidenav-responsive.component.html',
  styleUrls: ['./toolbar-and-sidenav-responsive.component.scss']
})
export class ToolbarAndSidenavResponsiveComponent implements OnInit, OnDestroy {
  theme: string = 'light-theme';
  mobileQuery: MediaQueryList;
  private _mobileQueryListener: () => void;

  constructor(changeDetectorRef: ChangeDetectorRef, media: MediaMatcher) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }

  ngOnInit() {
  }

  changeTheme(val: string) {
    if (val === 'light') {
      this.theme = 'light-theme';
    }
    if (val === 'dark') {
      this.theme = 'dark-theme';
    }
  }

}
